import React from "react";
import { useState, useEffect, useCallback } from "react";
import Swal from 'sweetalert2';
import { ethers } from 'ethers';
import NFT from './artifacts/contracts/NFTs.sol/NFTs.json';
import getToken from './helpers/getToken';
// import getPosts from './helpers/getPosts';

const FetchCard = (props) => {

    const [object, setObject] = useState({
        name: 'name',
        description: 'description',
        image: 'image',
        scarcity: 'scarcity'
      });
    const nftContract = "0x3636cd3a9D958558AF54A0829ddB003F41983e83";



        if(props.idsTokens) {
            console.log('el array de props ', props.idsTokens)

        }

      async function fetchToken(idToken)  {

        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const contract = new ethers.Contract(nftContract, NFT.abi, signer);

          try {

            const tokenUri = await contract.tokenURI(idToken);
             updateToken(tokenUri);

          } catch (err) {
            let mensajeError = err.message;
            Swal.fire({
              title: 'Ooops!',
              text: `${mensajeError}`,
              icon: 'error',
              confirmButtonText: 'Cerrar'
            })
            console.log("Error: ", err)
          }
          
   
  }

    //   fetch('https://gateway.pinata.cloud/ipfs/QmS53vh6DqZBsa1FjrAjm3mVgJ5gfy7KdXEm8fUYoehMCg')
    // .then((res) => res.json())
    // .then((data) => {
   // console.log(data);
    //   setObject({
    //     name: data.name,
    //     description: data.description,
    //     image: data.image,
    //     scarcity: data.attributes[0].value
    //   });
    //});

  

    // const [user, setUser] = useState({});
    // const [post, setPost] = useState([]);

    // const apiToken = () => {
    //     getToken().then((newToken) => {
    //         setObject({
    //             name: newToken.name,
    //             description: newToken.description,
    //             image: newToken.image,
    //             scarcity: newToken.attributes[0].value
    //           });
    //     })
    // }

    const updateToken = (url) => {
        getToken(url)
            .then(
                (newToken) => {
                  console.log('este es el metadata del token', newToken);
                    setObject(
                        {
                            name: newToken.name,
                            description: newToken.description,
                            image: newToken.image,
                            rarity: newToken.attributes[0].value
                        });
                ;}
            )
    }

    // dudas con el useCallback mira https://youtu.be/6lvI-gTF_X8?t=2087
    // const apiPost = useCallback(() => {
    //     getPosts(user.id)
    //     .then((newposts) => {
    //          setPost(newposts);
    //     })
    // }, [user.id]);


    useEffect(() => {

        fetchToken(props.idToken);
    }, []);

    
    // useEffect(() => {
    //     if(user?.id) {
    //     apiPost();
    //     }
    // }, [user, apiPost]);


    return (
      
        <div key={object.name} className="col-lg-4 text-center text-lg-start">
            <div className="card mb-3" style={{maxWidth: "500px"}}>
                <div className="row g-0">
                    <div className="col-md-4">
                    <img src={object.image} className="img-fluid rounded-start" alt={object.name}/>
                    </div>
                    <div className="col-md-8">
                        <div className="card-body">
                        <h5 className="card-title">{object.name}</h5>
                        <p className="card-text">{object.description}</p>
                        <p className="card-text"><small className="text-muted">{object.rarity}</small></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FetchCard