import React from "react";
import { useEffect, useState } from 'react';
import './App.css';
import Swal from 'sweetalert2';
import VideoPlayer from "react-background-video-player";
import { ethers } from 'ethers';
import NFT from './artifacts/contracts/NFTs.sol/NFTs.json';
import Card from './Card';
import FetchCard from './FetchCard';


function App() {
  
  const BINANCETESTNET = 'bnbt'
  const nftContract = "0x3636cd3a9D958558AF54A0829ddB003F41983e83";
  const [balance, setBalance] = useState(null);
  const [network, setNetwork] = useState('no-net');
  const [object, setObject] = useState({
    name: 'name',
    description: 'description',
    image: 'image',
    scarcity: 'scarcity'
  });
  const [offSetY, setOffSetY] = useState(0);
  const handleScroll = () => setOffSetY(window.scrollY);


  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    }
  }, [])
  
  useEffect(() => {
  
    // fetch('https://gateway.pinata.cloud/ipfs/QmS53vh6DqZBsa1FjrAjm3mVgJ5gfy7KdXEm8fUYoehMCg')
    // .then((res) => res.json())
    // .then((data) => {
    // console.log(data);
    //   setObject({
    //     name: data.name,
    //     description: data.description,
    //     image: data.image,
    //     scarcity: data.attributes[0].value
    //   });
    // });
   
  },[]);    

  useEffect(function () {

    

    if (typeof window.ethereum !== 'undefined') {
      
    changeAccounts();
    checkingNetwork();
 
    } else {
            Swal.fire({
              title: "No tiene metamask instalado",
              text: "Cambie de navegador o puede instalárselo apretando al botón",
              showCancelButton: true,
              confirmButtonText: "Instalar",
              imageUrl: './img/metamask-transparent.png',
              imageAlt: "Instalar metamask",
            }).then((result) => {
              /* Read more about isConfirmed, isDenied below */
              if (result.isConfirmed) {
                window.open('https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn', '_blank');  
              }})};
        
      },[]);

  useEffect(function () {

    
      if(network !== BINANCETESTNET && network !== 'no-net' ){ 

          Swal.fire({
            title: '¡Atencion!',
            //text: `Cambia a BSC si la tienes o sigue el siguiente tutorial para configurarla`,
            text: "Estas en la red "+network,
            showCancelButton: true,
            confirmButtonText: "Instalar o cambiar a la red Testnet BSC",
            //imageUrl: 'https://i2.wp.com/criptotendencia.com/wp-content/uploads/2020/04/binance-smart-chain.jpg?fit=1200%2C674&ssl=1',
            imageUrl: 'https://cryptodaily.io/wp-content/uploads/2021/07/p-2.png',
            imageWidth: 300,
            
            imageAlt: 'Red Binance Smart Chain',
          }).then((result) => {
            /* Read more about isConfirmed, isDenied below */
            if (result.isConfirmed) {
              addNetwork();
              //window.open('https://academy.binance.com/es/articles/connecting-metamask-to-binance-smart-chain', '_blank');  
            }
          })
        } else {

        console.log('ok network ', network );
      }
        
        console.log('Control de network ', network );

},[network]);

  async function addNetwork() {

    let networkData = [{
            chainId: "0x61",
            chainName: "BSCTESTNET",
            rpcUrls: ["https://data-seed-prebsc-2-s3.binance.org:8545"],
            nativeCurrency: {
              name: "BINANCE COIN",
              symbol: "BNB",
              decimals: 18,
            },
            blockExplorerUrls: ["https://testnet.bscscan.com/"],
          },
        ];

      // agregar red o cambiar red
      return window.ethereum.request({
        method: "wallet_addEthereumChain",
        params: networkData,
      });
  }

  async function checkingNetwork() {

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    // capturamos la red
    const network = await provider.getNetwork();
    console.log('network', network.name);
    setNetwork(network.name);
  }

  async function init() {  

            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            let accountConnection = accounts[0];
            let subint = accountConnection.substr(0,4);
            let subfinal = accountConnection.substr(-4,4);
            document.querySelector('#intro').innerHTML ='Conectado con la cuenta: ' + subint + '...' + subfinal;
               // comprobacion de NFTs
            const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(nftContract, NFT.abi, signer);

            try {
              const contractUserBalance = await contract.tokensOfOwner(account);

              if(contractUserBalance.length > 0 && !balance) {
                document.querySelector('#tokens').innerHTML ='Tienes avatares ';
                let newArray = Object.values(contractUserBalance);
                let element = document.querySelector("#buy");
                element.classList.add("d-none");
                
                setBalance(newArray);
               
              }    else {
                document.querySelector('#tokens').innerHTML ='Sin avatares';
                let element = document.querySelector("#buy");
                element.classList.remove("d-none");
              }

            } catch (err) {
              let mensajeError = err.message;
              Swal.fire({
                title: 'Ooops!',
                text: `${mensajeError}`,
                icon: 'error',
                confirmButtonText: 'Cerrar'
              })
              console.log("Error: ", err)
            }
                
  }


    // funcion que detecta los cambios de cuenta
    async function changeAccounts() {
          
      if (typeof window.ethereum !== 'undefined') {

        window.ethereum.on("accountsChanged", async function () {

          await init();
          
        });

      }
    }

    function faucet() {
      window.open('https://testnet.binance.org/faucet-smart', '_blank');  
    }



  return (

<div className="frame container">
        <div className="main row align-items-center g-lg-5 py-5" style={{transform: `translateY(-${offSetY * 0.3}px)`}}>
          <div className="col-lg-4 text-center text-lg-start">
            <form className="p-4 p-md-5 border rounded-3 bg-light">
            <div className="form-floating mb-3">
            <h2 id="intro" className="text-muted">No conectado</h2>
            </div>
            <hr className="my-4"/>
            <h3 id="tokens" className="text-muted"></h3>
            </form>
          </div>
          <div className="col-lg-4 text-center text-lg-start">
            <button id="btn-firma"  onClick={() => init()} className="w-100 btn btn-lg btn-danger mb-4" type="button">Conectar Metamask</button>
            {/* <button id="bnbNetwork"  onClick={() => addNetwork()} className="w-100 btn btn-lg btn-warning mb-4" type="button">Añadir Red Binance Smart Chain de pruebas</button> */}
            <button id="faucet"  onClick={() => faucet()} className="w-100 btn btn-lg btn-primary" type="button">Faucet de Binance</button>
            <button  id="buy" className="w-100 btn btn-lg btn-success d-none my-4" type="button">Comprar Avatares</button>
          </div>
        </div>
        <div className="row align-items-center g-lg-5 py-5"> 
            {
          balance &&
  
              balance.map((idToken) => {
                return     <FetchCard idToken={idToken} />
              })
 
            }
        </div> 
    
    
    

    
    <VideoPlayer style={{transform: `translateY(${offSetY * 1}px)`}}
        className="Video"
        src={
          "https://fishervspirate.com//wp-content//uploads//WhatsApp-Video-2021-09-11-at-11.17.45.mp4"
        }
        autoPlay={true}
        muted={true}
      />
</div>

  );
}

export default App;
